import React, { Component } from 'react'
import About from './About.js'

import './Nav.css'

class Nav extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showAbout: false
    }
    this.toggleAbout = this.toggleAbout.bind(this)
  }

  render() {
    const styles = {
      float: 'left',
      height: '50px',
      lineHeight: '20px',
      padding: '15px 15px'
    }

    const about = this.state.showAbout ? <About/> : null

    return (
      <nav className="navbar navbar-inclucivics" role="navigation">
        <div className="container">
          <a className="navbar-brand" href="#" id="home-link">IncluCivics</a>
          <a style={styles} onClick={this.toggleAbout}>About <span className="hidden-xs">This Project</span></a>
          {about}
        </div>
      </nav>
    )
  }

  toggleAbout() {
    this.setState({showAbout: !this.state.showAbout})
  }
}

export default Nav;



